import _ from 'lodash';

const images = {
  squaredBox: {
    icon: require('../../assets/sideProjects/squaredBox/icon.png'),
  },
  ktmBuddy: {
    icon: require('../../assets/sideProjects/ktmBuddy/icon.png'),
  },
};

let myApps = [
  {
    id: 'squaredBox',
    cover: null,
    icon: images.squaredBox.icon,
    title: 'Squared Box',
    subtitle: 'The Best Color Puzzle!',
    website: '',
    description: "The best Color Puzzle ever! Perfect mini game to play while waiting in a queue or when you are commuting with a train. Think logically and move logically!",
    iosLink: 'https://itunes.apple.com/us/app/squared-box/id1424904552?ls=1&mt=8',
    androidLink: '',
    sliderPosition: 1,
  },
  {
    id: 'ktmBuddy',
    cover: null,
    icon: images.ktmBuddy.icon,
    title: 'KTMBuddy',
    subtitle: 'Your everyday commute made easy.',
    website: '',
    description: "Check KTM train timetable without hassle. With KTM train timetable effective 03 Jun 2018. Available routes: Blue line and Red line of KTMB train.",
    iosLink: 'https://itunes.apple.com/us/app/ktm-buddy/id1434968365?ls=1&mt=8',
    androidLink: 'https://play.google.com/store/apps/details?id=com.chinloongtan.app.ktmbuddy',
    sliderPosition: 2,
  },
];

myApps = _.filter(myApps, (app) => (!app.hidden));
myApps = _.sortBy(myApps, ['sliderPosition']).reverse();

export const apps = myApps;



